@import '../../styles//variaveis.scss';
.cabecalho {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 1.5rem 24px;
  
    &__container {
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding: 6px 10px;
      width: 33%;
      position: relative;
      z-index: 0;
  
      &::before {
        content: "";
        position: absolute;
        inset: 0;
        border-radius: 10px;
        z-index: -1;
        border: 2px solid transparent;
        background: linear-gradient(to right, $rosa,$roxo) border-box;
        -webkit-mask: linear-gradient($branco 0 0) padding-box,
          linear-gradient($branco 0 0);
        -webkit-mask-composite: xor;
        mask-composite: exclude;
      }
    }
  
    &__input {
      width: 80%;
      height: 30px;
      color: $cor-do-texto;
      font-size: 1rem;
      font-family: $fonte-normal;
      border: none;
      background-color: transparent;
      outline: 0;
  
      &::placeholder {
        font-size: 1rem;
      }
    }
  
    img {
      cursor: pointer;
    }
  }