@import "../../styles/variaveis";
.botao {
    margin-top: 1.5rem;
    background: transparent;
    border: none;
    color: $cor-do-texto;
    font-family: $fonte-normal;
    font-size: 16px;
    width: 100%;
    padding: 12px 16px;
    position: relative;
    z-index: 0;
    cursor: pointer;

    &:hover {
      border-radius: 10px;
      background: linear-gradient(to right, $rosa, $roxo) border-box;
      color: $azul-escuro;
      transition: 0.1s;
    }

    &::before {
    content: "";
    position: absolute;
    inset: 0;
    border-radius: 10px;
    z-index: -1;
    border: 2px solid transparent;
    background: linear-gradient(to right, #c98cf1, #7b78e5) border-box;
    -webkit-mask: linear-gradient(#fff 0 0) padding-box,
      linear-gradient(#fff 0 0);
    -webkit-mask-composite: xor;
    mask-composite: exclude;
  }
}